import React, { Component } from 'react'
import { Link } from 'react-router'
import Helmet from 'react-helmet'

import logoXS from './logo-xs.png'

class Menu extends Component {

    render() {
        let logo;
        if (this.props.showLogo) {
            logo = <Link to="/" activeClassName="active" className="navbar-left">
                <img src={logoXS} role="presentation" />
            </Link>
        }

        return (
            <nav className="navbar navbar-default navbar-static-top navbar-menu">
                <Helmet
                    htmlAttributes={{lang: 'en'}}
                    link={[
                        { rel: 'icon', type: 'image/png', href: '/favicon-32x32.png', size: '32x32'}
                    ]}
                    title="Home"
                    titleTemplate="%s | Coffee Bean Ninja"
                    meta={[
                        {charset: 'utf-8' },
                        {name: 'viewport', content: 'width=device-width,initial-scale=1'},
                        {name: 'application-name', content:"Coffee Bean Ninja"},
                        {name: 'author', content: 'Sander Huijsen'},
                        {name: 'description', content: 'Find amazing new beans for your coffee and places to buy them.'},
                        {property: "og:type", content: "website"}
                    ]}
                    script={[
                        {type: 'text/javascript', innerHTML: `!function(e,t,a,n,c,o,s){e.GoogleAnalyticsObject=c,e[c]=e[c]||function(){(e[c].q=e[c].q||[]).push(arguments)},e[c].l=1*new Date,o=t.createElement(a),s=t.getElementsByTagName(a)[0],o.async=1,o.src=n,s.parentNode.insertBefore(o,s)}(window,document,"script","https://www.google-analytics.com/analytics.js","ga"),ga("create","UA-89044440-1","auto"),ga("send","pageview")`}
                    ]}
                />
                <div className="container">
                    {logo}
                    <div id="navbar-collapse" className="collapse navbar-collapse">
                        {/*<ul className="nav navbar-nav">*/}
                            {/*<li><Link to="/" activeClassName="active" onlyActiveOnIndex={true}>Home</Link></li>*/}

                        {/*</ul>*/}
                        <ul className="nav navbar-nav navbar-right">
                            <li><Link to="/login" activeClassName="active">Login</Link></li>
                        </ul>
                    </div>
                </div>
            </nav>
        )
    }
}

Menu.propTypes = {
    showLogo: React.PropTypes.bool
};

Menu.defaultProps = {
    showLogo: true
};

export default Menu;



