import reducer from './reducers';
import routes from './routes';
import thunk from 'redux-thunk';

import 'bootstrap/dist/css/bootstrap.css';
import './index.css';

const config = {
	redux: {
		reducer: reducer,
		middleware: [ thunk ]
	},
	router: {
		routes: routes	
	}
};

export default config


